.hero {
  text-align: center;
  padding: 4rem 2rem;
  background: linear-gradient(135deg, #00adb5, #393e46); /* Teal to Dark Gray */
  color: #ffffff;
}

.hero h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

.hero p {
  font-size: 1.2rem;
  margin-bottom: 2rem;
}

.featured-products {
  text-align: center;
  padding: 4rem;
  background-color: #eeeeee; /* Light Gray */
}

.featured-products h2 {
  font-size: 2rem;
  color: #393e46; /* Dark Gray */
}

.products {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin-top: 1.5rem;
}

.product {
  padding: 1rem;
  background-color: #ffe0b5; /* Peach */
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  font-size: 1.1rem;
  font-weight: bold;
  color: #1b262c; /* Deep Navy */
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  text-align: center;
}

.product:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

.product-image {
  width: 100px; /* Customize size */
  height: auto; /* Maintain aspect ratio */
  border: 2px solid #393e46; /* Dark Gray border */
  border-radius: 8px; /* Rounded corners */
  margin-bottom: 0.5rem; /* Spacing between image and text */
}