.contact {
    padding: 2rem;
    text-align: center;
    background: linear-gradient(135deg, #ffe0b5, #00adb5); /* Peach to Teal */
    color: #ffffff;
  }
  
  .contact h1 {
    font-size: 2.5rem;
    margin-bottom: 1rem;
  }
  
  .contact form {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    width: 100%;
    max-width: 500px;
    margin: 0 auto;
    padding: 2.3rem;
  }
  
  .contact input,
  .contact textarea {
    width: 100%;
    padding: 0.8rem;
    border-radius: 5px;
    border: none;
    font-size: 1rem;
  }
  
  .contact textarea {
    height: 100px;
  }
  
  .contact button {
    background-color: #1b262c; /* Deep Navy */
    color: #ffffff;
    padding: 0.8rem 1.5rem;
    border: none;
    border-radius: 5px;
    font-size: 1.2rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .contact button:hover {
    background-color: #00adb5; /* Teal */
  }